
import BScroll from 'better-scroll'
import LoadMore from 'vux/src/components/load-more/index.vue'

let count = 1
export default {
name: 'houseList',
components: {
LoadMore
},
data () {
return {
isMoreData: false,
data: [],
pulldownMsg: '下拉刷新',
pullupMsg: '加载更多',
isfirstInit: true,
alertHook: 'none',
isSale: 0,
stopSale: 0,
waitSale: 0,
params: {
merchantId: '',
status: 1, // 1-发布 2-下架 3-待完善
page: 1,
pageSize: 10
},
tabIndex: 1
}
},
methods: {
edit(item) {
this.$store.state.tabIndex = this.tabIndex
this.$router.push({
name: 'houseAdd',
query: {
params: {
id: item.id
}
}
})
event.stopPropagation()
},
getOff(item,status) {
this.$vux.loading.show({
text: '加载中...'
})
let txt = ''
if(status == 2) {
txt = '下架成功'
}
if( status == 1) {
txt = '上架成功'
}
this.$http.fetchGet('/merchant/room/update/status', {roomId: item.id, operate: status}).then((res) => {
this.$vux.loading.hide()
if(res.data.code == 200) {
this.$vux.toast.show({
text: txt,
position: 'middle'
})
this.refreshData()
} else {
this.$vux.toast.show({
text: res.data.message,
position: 'middle',
type: 'warn'
})
}
})
event.stopPropagation()
},
deleteList(item) {
this.$vux.confirm.show({
title : '此操作将永久删除，是否继续？',
onCancel : () => {

},
onConfirm : () => {
this.$vux.loading.show({
text: '加载中...'
})
this.$http.fetchPost('/merchant/room/delete/room?roomId='+item.id).then((res)=>{
this.$vux.loading.hide()
if(res.data.code == 200) {
this.$vux.toast.show({
text: '删除成功',
position: 'middle'
})
this.refreshData()
} else {
this.$vux.toast.show({
text: res.data.message,
position: 'middle',
type: 'warn'
})
}
})
}
})
event.stopPropagation()
},
addHouse () {
this.$store.state.tabIndex = this.tabIndex
this.$router.push('houseAdd')
},
goDetails (id,status,dataRemark) {
this.$store.state.tabIndex = this.tabIndex
this.$router.push({
name: 'houseDetail',
query: {
id: id,
tabIndex: this.tabIndex,
status: status,
dataRemark: dataRemark
}
})
},
getBack () {
this.$router.go(-1)
},
refreshalert () { // 刷新成功提示
this.alertHook = 'block'
setTimeout(() => {
this.alertHook = 'none'
}, 1000)
},
getData () {
this.$http.fetchGet('/merchant/room/get/list', this.params).then((res) => {
setTimeout(() => {
this.$vux.loading.hide()
}, 500)
this.isSale = res.data.data.roomOnNum
this.stopSale = res.data.data.roomDownNum
this.waitSale = res.data.data.roomProcessNum
this.data = res.data.data.roomsList
if (this.data.length === this.params.pageSize) {
this.isMoreData = true
} else {
this.isMoreData = false
this.$nextTick(() => {
if (this.$refs.list_con.offsetHeight > this.$refs.content.offsetHeight) {
this.$refs.content.style.height = this.$refs.list_con.offsetHeight + 2 + 'px'
}
})
}
this.initScroll()
})
},
tabChange (tabIndex) {
this.$vux.loading.show({
text: '加载中...'
})
this.$refs.content.style.height = 'auto'
this.tabIndex = tabIndex
this.params.page = 1
this.refreshData()
},
refreshData () {
this.params.status = this.tabIndex
this.$refs.content.style.height = 'auto'
this.pullupMsg = '加载中。。。'
this.params.page = 1
this.$http.fetchGet('/merchant/room/get/list', this.params).then((res) => {
setTimeout(() => {
this.$vux.loading.hide()
}, 500)
this.isSale = res.data.data.roomOnNum
this.stopSale = res.data.data.roomDownNum
this.waitSale = res.data.data.roomProcessNum
this.data = res.data.data.roomsList
if (this.data.length === this.params.pageSize) {
this.isMoreData = true
} else {
this.isMoreData = false
this.$nextTick(() => {
if (this.$refs.list_con.offsetHeight > this.$refs.content.offsetHeight) {
this.$refs.content.style.height = this.$refs.list_con.offsetHeight + 2 + 'px'
}
})
}

this.pullupMsg = '加载更多'

this.scroll.refresh()
})
},
loadMoreData () {
if (this.data.length < this.params.pageSize || this.data.length < this.params.pageSize * this.params.page) {
this.isMoreData = false
return
}
this.$vux.loading.show({
text: '加载中...'
})
this.params.page++
this.$http.fetchGet('/merchant/room/get/list', this.params).then((res) => {
setTimeout(() => {
this.$vux.loading.hide()
}, 500)
this.isSale = res.data.data.roomOnNum
this.stopSale = res.data.data.roomDownNum
this.waitSale = res.data.data.roomProcessNum
if (this.data.length === this.params.pageSize) {
this.isMoreData = true
} else {
this.isMoreData = false
}
if (res.data.data.roomsList.length > 0) {
res.data.data.roomsList.map((item) => {
this.data.push(item)
})
} else {
this.isMoreData = false
}

this.pulldownMsg = '下拉刷新'

this.scroll.refresh()
})
},
initScroll () {
this.$nextTick(() => {
this.scroll = new BScroll(this.$refs.wrapper, { // 初始化better-scroll
probeType: 1, // 1 滚动的时候会派发scroll事件，会截流。2滚动的时候实时派发scroll事件，不会截流。 3除了实时派发scroll事件，在swipe的情况下仍然能实时派发scroll事件
click: true 
})

this.scroll.on('scroll', (pos) => {
if (pos.y > 30) {
this.pulldownMsg = '释放立即刷新'
}
})

this.scroll.on('touchEnd', (pos) => { // 上拉刷新
if (pos.y > 30) {
this.refreshData()
} else if (pos.y < (this.scroll.maxScrollY - 30)) { // 下拉加载
this.loadMoreData()
}
})
})
}
},
created () {
this.$vux.loading.show({
text: '加载中...'
})
this.params.merchantId = this.$store.state.merchantId
if(this.$store.state.tabIndex) {
this.tabIndex = this.$store.state.tabIndex
this.params.status = this.$store.state.tabIndex
this.$store.state.tabIndex = ''
}
this.getData()
}
}



